/* ==============================
   GENERIC HERO SHORTCODE
   ============================== */

@keyframes generic-bg-movement {
  0% { transform: translateX(0); }
  20% { transform: translateX(20vw); }
  60% { transform: translateX(-20vw); }
  80% { transform: translateX(10vw); }
  100% { transform: translateX(-5vw); }
}

@keyframes generic-bg-opacity {
  0% { opacity: 0 }
  25% { opacity: .8 }
  50% { opacity: .3 }
  75% { opacity: .5 }
  100% { opacity: .2 }
}

.uri-generic-hero {

  position: relative;
  margin: 0 0 2rem;

  * {
     box-sizing: border-box;
  }

  .uri-generic-hero-backdrop {

    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background: linear-gradient( to bottom, #6423a6, #002147);

    div {

      position: absolute;
      mix-blend-mode: soft-light;
      height: 100%;
      background: linear-gradient(to right,
        rgba(255,255,255,0) 0%,
        rgba(255,255,255,0.1) 20%,
        rgba(255,255,255,1) 50%,
        rgba(255,255,255,0.1) 80%,
        rgba(255,255,255,0) 100%
      );
      animation-name: generic-bg-movement, generic-bg-opacity;
      animation-iteration-count: infinite;
      animation-timing-function: ease-in-out;
      animation-direction: alternate;
      animation-fill-mode: forwards;

      &.speed-low {
        animation-duration: 30s;
      }

      &.speed-medium {
        animation-duration: 25s;
      }

      &.speed-high {
        animation-duration: 20s;
      }
    }
  }

  .uri-generic-hero-proper {

    display: grid;
  	width: 100vw;
  	overflow: hidden;
  	position: relative;
  	z-index: 1;

  }

  .uri-generic-hero-text {

    place-self: center;
    padding: 2rem;

    h1 {
      font-size: 4rem;
      color: #fff;
    }

    p {
      color: #fff;
      width: 80%;
      max-width: 500px;
    }

    .uri-generic-hero-button {
      display: inline-block;
      padding: 1rem;
      background: #fff;
    }

    .cl-button {
      padding: 1rem 2rem;
      width: initial;
      max-width: initial;
    }

  }

}
